@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
body {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-family: "Nunito Sans", sans-serif;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
    height: 100vh;
}

.App {
  height: 100%;
  display: flex;
  color: rgba(189, 189, 192, 1);

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.side-bar {
    width: 80px;
    background-color: rgba(32, 32, 35, 1);
    height: 100%;
    border-right: 1px solid rgba(189, 189, 192, 0.1);
    -webkit-transition: width 0.3s;
    transition: width 0.3s;
    overflow-y: scroll;
}
/* not small devices  */
@media (min-width: 576px) {
    .side-bar {
        width: 320px;
    } }

.user {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
}
.user:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}
.user__pic {
    width: 50px;
    border-radius: 50%;
}
.user__details {
    display: none;
}


/* not small devices  */

@media (min-width: 576px) {
    .user__details {
        display: block;
        padding: 0 0 0 1rem;
    }
    .user__details-name {
        margin: 0;
        color: rgba(255, 255, 255, 0.8);
        font-size: 1rem;
    } }



.main {
    flex: 1 1;
    background-color: rgba(25, 25, 27, 1);
    height: 100%;
}
.empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.empty__name {
    color: #fff;
}
.empty__status,
.empty__info {
    padding: 1rem;
}
.empty__status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
}
.empty__img {
    border-radius: 50%;
    margin: 2rem 0;
}
.empty__btn {
    padding: 1rem;
    margin: 1rem 0;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 30px;
    outline: 0;
}
.empty__btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
}
.Header {
    padding: 1rem 2rem;
    border-bottom: 1px solid rgba(189, 189, 192, 0.2);
}
.Header__name {
    color: #fff;
}

.Chats {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 85%;
    margin: 0 auto;
    overflow-y: scroll;
}
.Chat {
    margin: 1rem 0;
    color: #fff;
    padding: 1rem;
    background: -webkit-linear-gradient(left, #1986d8, #7b9cc2);
    background: linear-gradient(90deg, #1986d8, #7b9cc2);
    max-width: 90%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.Chat.is-user-msg {
    margin-left: auto;
    background: #2b2c33;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
@media (min-width: 576px) {
    .Chat {
        max-width: 60%;
    }
}

.chat-window {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.Message {
    width: 80%;
    margin: 1rem auto;
}
.Message__input {
    width: 100%;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border: 0;
    border-radius: 10px;
    font-size: 1rem;
    outline: 0;
}
